[data-theme="dark"] {
  --frontpage-search-submit-icon: var(--icon-magnify--white); }

.frontpage-hero {
  margin: var(--spacing-xl) 0; }

.frontpage-hero__container {
  display: grid;
  grid-gap: var(--spacing-xl);
  grid-template-columns: 2fr 2fr 1fr; }

.frontpage-hero__title {
  font-weight: bold; }

.frontpage-hero__actions {
  margin-top: var(--spacing-md);
  display: grid;
  grid-gap: var(--grid-gap);
  grid-template-columns: repeat(2, 1fr); }

.frontpage-hero__button {
  width: 100%; }

.frontpage-search {
  position: relative; }

.frontpage-search__input {
  width: 100%;
  padding-right: calc(3rem + var(--spacing-sm)); }

.frontpage-search__submit {
  position: absolute;
  top: 0;
  right: 0;
  width: 3rem;
  height: 100%;
  padding: 0;
  border: unset;
  border-radius: var(--radius-xs);
  background-color: transparent;
  background-image: var(--frontpage-search-submit-icon);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 1.5rem; }

.official-user-guide {
  background-color: var(--color-persian-blue--light-3);
  border-radius: var(--radius-md);
  display: grid;
  grid-gap: var(--spacing-xl);
  grid-template-columns: 500px 1fr;
  padding: var(--spacing-xl); }

.official-user-guide__link {
  display: flex;
  align-items: center; }

.official-user-guide__link:not(:last-child) {
  margin-bottom: var(--spacing-sm); }

.official-user-guide__link-icon {
  flex-shrink: 0;
  margin-right: var(--spacing-sm);
  width: 60px;
  height: 60px;
  background-color: var(--color-persian-blue);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 40px;
  border-radius: 50%;
  box-shadow: var(--shadow-xs); }

.official-user-guide__link-icon--read {
  background-image: var(--icon-web--white); }

.official-user-guide__link-icon--download {
  background-image: var(--icon-download-outline--white); }

.official-user-guide__link-title {
  color: var(--text-color);
  font-size: var(--text-lg);
  font-weight: bold;
  text-decoration: underline; }

.official-user-guide__link-description {
  font-size: var(--text-xs);
  color: var(--color-gray-70); }
